import Vue from 'vue';
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    mealsArray: [],
    selectedMeal: {
      mealName: "",
      mealImageUrl: "",
      mealIngredients: [],     
      mealRecipeInstructions: []
    },
    showResults: false,
    searchTerm: "",
    windowWidth: window.innerWidth
  },
  getters: {
    checkForMealFound: state => {
      if(state.showResults) {
        return true;
      } else {
        return false;
      }
    },
    getMealsArray: state => {
      return state.mealsArray;
    },
    getSearchTerm: state => {
      return state.searchTerm;
    }
  },
  mutations: {
    hideMealDetails: state => {
      state.showResults = false;
    },
    showMealDetails: state => {
      state.showResults = true;
    },
    addMeals: (state, newMeal) => {
      state.mealsArray = newMeal;
    },
    updateSelectedMeal: (state, selectedMeal) => {
      state.selectedMeal.mealName = selectedMeal.strMeal;
      state.selectedMeal.mealImageUrl = selectedMeal.strMealThumb;    
    },
    clearSelectedMeal: state => {
      state.selectedMeal.mealName = "";
    },
    updateSearchTerm: (state, newSearchTerm) => {
      state.searchTerm = newSearchTerm;
    },
    updateIngredientsArray: (state, ingredient) => {
      state.selectedMeal.mealIngredients.push(ingredient);
    },
    clearIngredientsArray: state => {
      while (state.selectedMeal.mealIngredients.length) {
        state.selectedMeal.mealIngredients.pop();
      }  
    },
    updateInstructionsArray: (state, instructionLine) => {
      state.selectedMeal.mealRecipeInstructions.push(instructionLine);
    },
    clearInstructionsArray: state => {
      while (state.selectedMeal.mealRecipeInstructions.length) {
        state.selectedMeal.mealRecipeInstructions.pop();
      }
    }
  }
});