<template>

  <div>
      <!-- Navigation -->
      <nav class="navbar navbar-expand-lg navbar-light bg-light shadow fixed-top">
        <div class="container">
            <img class="navBarImg" alt="logo" />
            <a class="navbar-brand" href="#">{{ title }}</a>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarResponsive">
              <ul class="navbar-nav ml-auto">
                  <li class="nav-item active">
                  <a class="nav-link" href="#">Home
                      <span class="sr-only">(current)</span>
                  </a>
                  </li>
                  <li class="nav-item">
                    <a class="nav-link" href="#">About</a>
                  </li>
                  <li class="nav-item">
                    <a class="nav-link" href="#">Services</a>
                  </li>
                  <li class="nav-item">
                    <a class="nav-link" href="#">Contact</a>
                  </li>
              </ul>
            </div>
        </div>
      </nav>

      <!-- Full Page Image Header with Vertically Centered Content -->
      <header class="masthead" v-bind:class="{ mastheadHalf: isMealFound }">
        <div class="container h-100">
          <div class="row h-100 align-items-center">
            <div class="col-12 text-center">

                <h1 class="font-weight-light">Looking for a little inspiration for a tasty meal today?</h1>
                <h1 class="lead">Enter some ingredients and find a great meal with a new recipe!</h1>

                <div class="row justify-content-md-center">
                  <div class="input-group mt-4 col-md-5">
                    <input ref="searchBox" v-model="searchIngredient" type="text" class="form-control" placeholder="Enter an ingredient, i.e. Steak" aria-label="Enter an ingredient, i.e. Steak">
                  
                    <div class="input-group-append">
                      <button v-on:click="mealSearch" class="btn btn-success" type="button">Search for Meal</button>
                    </div>
                  </div>                            
                </div>

            </div>
          </div>

        </div>
      </header>
  </div>

</template>

<script>
  export default {
    name: 'Header',
    props: {
      title: String
    },
    data: function() {
      return {
        searchIngredient: "",
      }
    },
    mounted: function() {
      this.setFocusSearchBox();
    },
    methods: {
      mealSearch: function() {
        if(this.searchIngredient.trim().length > 0) {
          const baseURI = `https://www.themealdb.com/api/json/v1/1/search.php?s=${this.searchIngredient}`;

          this.$store.commit('clearSelectedMeal');

          this.$http.get(baseURI)
            .then((result) => {
              if(result.data.meals) {
                this.$store.commit('showMealDetails');   
                
                // Update state with meal data retrieved
                this.$store.commit('addMeals', result.data.meals);

              }else {
                this.$store.commit('hideMealDetails');
                this.$toast.error("No meals found!");                             
              }        
            });          
        }else {
          this.$store.commit('hideMealDetails');
          this.$toast.warning("Please enter an ingredient");
        }             
   
        this.$store.commit('updateSearchTerm', this.searchIngredient);
        this.setFocusSearchBox();      
      },

      setFocusSearchBox: function() {
        this.searchIngredient = "";
        this.$refs.searchBox.focus();
      }    
    },
    computed: {
      isMealFound() {
        return this.$store.getters.checkForMealFound;
      }
    }
  }
</script>

<style scoped>

  nav {
    opacity: 0.9;
  }

  .masthead {
    height: 100vh;
    min-height: 500px;
    background-image: url('../assets/showcase.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: white;
  }

  .mastheadHalf {
    height: 50vh;
    background-image: url('../assets/showcase.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-top: 100px;
    color: white;
  }
  
  .navBarImg {
    margin-right: 10px;
    height: 60px;
    content: url('../assets/logosmall.png');
  }

  .lead {
    font-size: 1.4em;
  }

</style>