<template>

    <!-- Page Body Content -->
    <section class="py-5" v-if="isMealFound">
      <div class="container">

        <h2 class="font-weight-light">Meals found</h2>
        <h4 class="font-weight-light">We have found the following meals which include the search term "<span class="search-text">{{ currentSearchTerm }}</span>"</h4>
     
        <div v-if="!this.$store.state.selectedMeal.mealName">

          <!-- Meal images cards for meals found matching search criteria -->          
          <div class="meal-images-container">
            <div class="meal-images card mb-2 mr-2" v-for="(meal,index) in meals" :key="meal.strMeal">
                <img class="card-img-top" v-bind:src='meal.strMealThumb'/>
                <div class="card-body card-text">
                  <button class="btn btn-outline-success" @click="getSelectedMeal(index)">{{meal.strMeal}}</button>
                </div>
            </div>  
          </div>          
        </div>

        <div v-if="this.$store.state.selectedMeal.mealName" class="mt-3">

          <!-- Carousel images -->
          <div class="mb-5">
            <VueSlickCarousel :arrows="true" :dots="true" :slidesToShow=10>
              <div 
                v-for="meal in meals" :key="meal.strMeal"
                class="carousel-pic" 
                v-bind:style="{ 'backgroundImage': 'url(' +  meal.strMealThumb + ')' }">
                <div class="carousel-pic-text">{{meal.strMeal}}</div>
              </div>
            </VueSlickCarousel>
          </div>

          <!-- Selected meal details -->
          <h4 class="alert alert-success text-center">{{this.$store.state.selectedMeal.mealName}}</h4>

          <div id="selected-meal-image-top" 
            v-if="$screen.breakpoint == 'sm'"
            v-bind:style="{ 'background-image': 'url(' + this.$store.state.selectedMeal.mealImageUrl +')' }">
          </div>

          <h4 class="mt-4">Ingredients:</h4>             
          <div class="ingredients-and-meal-image">
            <div id="selected-meal-ingredients col-6">
              <div>
                <div v-for="(ingredient, index) in this.$store.state.selectedMeal.mealIngredients" :key="'ingredient' + index">
                  {{ingredient.name}} ({{ingredient.measure}})
                </div>                  
              </div>                 
            </div>               

            <div id="selected-meal-image-side"
              v-if="$screen.breakpoint != 'sm'"
              v-bind:style="{ 'background-image': 'url(' + this.$store.state.selectedMeal.mealImageUrl +')' }" 
              class="col-6" >
            </div>             
          </div>

          <h4 class="mt-4">Instructions:</h4>
          <div v-for="(instructionLine, index) in this.$store.state.selectedMeal.mealRecipeInstructions" :key="'instruction' + index">
            {{instructionLine.instruction | preAppendNumber(index + 1)}}
          </div>

        </div>

      </div>
    </section>  
</template>

<script>

  import VueSlickCarousel from 'vue-slick-carousel'
  import 'vue-slick-carousel/dist/vue-slick-carousel.css'
  // optional style for arrows & dots
  import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

  export default {
    components: { VueSlickCarousel },
    name: 'Body',
    props: {
    },
    data: function() {
      return { 
        settings: {
          arrows: true,
          dots: true           
        }
      }
    },
    mounted: function() {
    },
    methods: {
      getSelectedMeal(mealIndex) {
        this.$store.commit('updateSelectedMeal', this.$store.state.mealsArray[mealIndex]); 

        // Store up to 20 meal ingredients in 'mealIngredients' array (in 'selectedMeal' object)
        this.$store.commit('clearIngredientsArray');
        for(var ingredientItem = 1; ingredientItem <= 20; ingredientItem++) {

          var strIngredient = 'strIngredient' + ingredientItem;
          var strMeasure = 'strMeasure' + ingredientItem         
          if(this.$store.state.mealsArray[mealIndex][strIngredient]) {
            this.$store.commit('updateIngredientsArray', 
              {
                name: this.$store.state.mealsArray[mealIndex][strIngredient], 
                measure: this.$store.state.mealsArray[mealIndex][strMeasure]
              });                      
          }
        }

        // Split instructions and store in 'mealRecipeInstructions' array (in 'selectedMeal' object)
        if(this.$store.state.mealsArray[mealIndex].strInstructions) {
          this.$store.commit('clearInstructionsArray');
          var instructionsArray = this.$store.state.mealsArray[mealIndex].strInstructions.split('.')
          for(var instructionsLine = 0; instructionsLine < instructionsArray.length -1; instructionsLine++) {
            this.$store.commit('updateInstructionsArray', 
              { 
                instruction: instructionsArray[instructionsLine].trim()+'.'
              });           
          }
        }

      }
    },
    computed: {
      meals(){
        return this.$store.getters.getMealsArray;
      },     
      isMealFound() {
        return this.$store.getters.checkForMealFound;
      },
      currentSearchTerm() {
        return this.$store.getters.getSearchTerm;
      }
    }
  }
</script>

<style scoped>

  .meal-images-container {
    display: flex;
    flex-wrap: wrap;
  }

  .meal-images img {
    height: 200px;
    width: 200px;
  }

  .card-text {
    width: 200px;
    word-wrap: wrap;
    text-align: center;
  }

  .card-body {
        background-color: whitesmoke;
        color: black;
  }

  .search-text {
    font-weight: bold;
  }

  .ingredients-and-meal-image {
    display: flex;
    align-content: space-between;
  }

  #selected-meal-ingredients {
    display: flex;
    align-items: flex-start;
  }

  #selected-meal-image-side {
    height: auto;
    width: 100%;
    /* background-image: url('../assets/testImage.jpg'); */
    background-position: center;
    background-repeat: no-repeat;
    color: white;
    margin-left: 20px;
    border-radius: 2%
  }

  #selected-meal-image-top {
    height: 200px;
    width: 100%;
    /* background-image: url('../assets/testImage.jpg'); */
    background-position: center;
    background-repeat: no-repeat;
    color: white;
    border-radius: 2%
  }

  .carousel-pic {
    width:100px;
    height: 100px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-style: solid;
    border-color: white;
  }

  .carousel-pic-text {
    color: white;
    text-align: center;
    font-weight: normal;
    background-color: #28A745;
    opacity: 0.8;
    width: 100%;
    line-height: 100%;
    padding: 2px;
  }

    .carousel-pic-text:hover {
    color: white;
    text-align: center;
    font-weight: normal;
    background-color: #28A745;
    opacity: 1;
    width: 100%;
    line-height: 100%;
    padding: 2px;
    cursor: pointer;
  }

</style>